.card {
    position: relative;
}
.card img {
    width: 100%;
    height: 150px;
    display: block;
    border: 2px solid white;
    border-radius: 6px;
}
/* Front */
.card .front {
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
}
.flipped .front {
    transform: rotateY(0deg);
    transition-delay: 0.2s;
}
/* Back */
.card .back {
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
}
.flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0s;
}